<template>
  <div id="editblog">
    <b-container>
      <b-row v-if="error">
        <b-card-text style="color: white;">Error fetching blog.</b-card-text>
      </b-row>
      <b-row v-if="!error">
        <b-col
          class="d-flex flex-column align-items-center justify-content-center"
          v-if="fetchLoading"
        >
          <b-spinner variant="success"></b-spinner>
        </b-col>
        <b-col
          v-if="!fetchLoading"
          class="d-flex flex-column align-items-center justify-content-center"
        >
          <b-card
            header-tag="header"
            footer-tag="footer"
            img-alt="Image"
            :img-src="oldImage"
            tag="article"
            class="mb-2"
            style="max-width: 40rem; width:100%;"
          >
            <form ref="form" @submit.stop.prevent="handleSubmit">
              <b-form-group
                :state="titleState"
                label="Title: "
                label-for="name-input"
                invalid-feedback="You need to enter a title"
              >
                <b-form-input id="name-input" v-model="title" :state="titleState" required></b-form-input>
              </b-form-group>
              <b-form-group label="Content: " label-for="content-input">
                <medium-editor
                  :text="content"
                  :options="options"
                  v-on:edit="processEditOperation"
                  custom-tag="div"
                />
              </b-form-group>
              <b-form-group
                :state="coverImageState"
                label="Cover Image:(Leave blank not to change current) "
                label-for="cover-image-input"
              >
                <b-form-file
                  id="cover-image-input"
                  accept="image/*"
                  browse-text="Select"
                  placeholder="Select or drag an image"
                  drop-placeholder="You can drop"
                  v-model="coverImageUrl"
                ></b-form-file>
              </b-form-group>
              <b-form-group
                label="Keywords: "
                label-for="keywords-input"
                invalid-feedback="Enter keywords comma-seperated "
              >
                <b-form-tags
                  id="keywords-input"
                  tag-pills
                  tag-variant="primary"
                  remove-on-delete
                  separator=","
                  duplicate-tag-text="Same keyword entered"
                  placeholder="Add via pressing enter or comma"
                  v-model="keywords"
                ></b-form-tags>
              </b-form-group>
            </form>
            <template v-slot:footer>
              <b-button-group>
                <b-button class="mr-2" @click.prevent="onSubmit" variant="success">
                  <b-spinner variant="danger" v-if="editLoading" small></b-spinner>
                  <span v-if="!editLoading">Submit</span>
                </b-button>
                <b-button to="/blog" variant="danger">Back</b-button>
              </b-button-group>
              <b-card-text>Created: {{when.toLocaleString("en-us")}}</b-card-text>
            </template>
          </b-card>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
import editor from "vue2-medium-editor";
import store from "../store/index";
export default {
  data: function() {
    return {
      options: {
        buttonLabels: "fontawesome",
        toolbar: {
          buttons: [
            "h1",
            "h2",
            "h3",
            "anchor",
            "bold",
            "italic",
            "unorderedlist",
            "orderedlist",
            "table"
          ]
        },
        extensions: {
          table: new MediumEditorTable()
        },
        placeholder: false
      },
      error: false,
      content: "",
      keywords: [],
      title: "",
      coverImageUrl: null,
      oldImage: "",
      editLoading: false,
      fetchLoading: false
    };
  },
  components: {
    "medium-editor": editor
  },
  computed: {
    titleState() {
      return this.title.length > 0;
    }
  },
  created() {
    this.fetchLoading = true;
    axios
      .get("/blog/blogEntries/" + this.$route.params.id)
      .then(res => {
        this.title = res.data.blogEntry.title;
        this.content = res.data.blogEntry.content;
        this.oldImage = res.data.blogEntry.coverImageUrl;
        this.keywords = res.data.blogEntry.keywords;
        this.when = res.data.blogEntry.when;
        this.fetchLoading = false;
      })
      .catch(err => {
        if (err.response) {
          if (
            err.response.data.error.statusCode == 422 &&
            err.response.data.error.problems.length > 0
          ) {
            let s = "";
            err.response.data.error.problems.forEach(x => {
              s += " * " + x.msg;
            });
            this.$bvToast.toast(err.response.data.message + ":     " + s);
          } else {
            this.$bvToast.toast(err.response.data.message);
          }
        } else {
          this.$bvToast.toast("An error occured: " + JSON.stringify(err));
        }
        this.error = true;
        this.fetchLoading = false;
      });
  },
  components: {
    "medium-editor": editor
  },
  methods: {
    toBase64(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
      });
    },
    processEditOperation(operation) {
      this.content = operation.api.origElements.innerHTML;
    },
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity();
      return valid;
    },
    async onSubmit() {
      if (!this.checkFormValidity()) {
        return;
      }
      this.editLoading = true;
      if (this.coverImageUrl == null) {
        axios
          .put("/blog/editBlogEntry/" + this.$route.params.id, {
            content: this.content,
            coverImageUrl: this.oldImage,
            title: this.title,
            keywords: this.keywords
          })
          .then(res => {
            store.commit("editBlog", {
              _id: this.$route.params.id,
              content: this.content,
              title: this.title,
              keywords: this.keywords
            });
            this.$bvToast.toast(res.data.message);
            this.editLoading = false;
          })
          .catch(err => {
            if (err.response) {
              if (
                err.response.data.error.statusCode == 422 &&
                err.response.data.error.problems.length > 0
              ) {
                let s = "";
                err.response.data.error.problems.forEach(x => {
                  s += " * " + x.msg;
                });
                this.$bvToast.toast(err.response.data.message + ":     " + s);
              } else {
                this.$bvToast.toast(err.response.data.message);
              }
            } else {
              this.$bvToast.toast("An error occured: " + JSON.stringify(err));
            }
            this.editLoading = false;
          });
      } else {
        let coverImageBase64;
        try {
          coverImageBase64 = await this.toBase64(this.coverImageUrl);
        } catch (error) {
          this.$bvToast.toast("An error occured: " + JSON.stringify(error));
          this.editLoading = false;
          return;
        }
        axios
          .put("/blog/editBlogEntry/" + this.$route.params.id, {
            content: this.content,
            coverImageUrl: coverImageBase64,
            title: this.title,
            keywords: this.keywords
          })
          .then(res => {
            store.commit("editBlogChangeCover", {
              _id: this.$route.params.id,
              content: this.content,
              title: this.title,
              coverImageUrl: this.coverImageUrl,
              keywords: this.keywords
            });
            this.$bvToast.toast(res.data.message);
            this.oldImage = coverImageBase64;
            this.editLoading = false;
          })
          .catch(err => {
            if (err.response) {
              if (
                err.response.data.error.statusCode == 422 &&
                err.response.data.error.problems.length > 0
              ) {
                let s = "";
                err.response.data.error.problems.forEach(x => {
                  s += " * " + x.msg;
                });
                this.$bvToast.toast(err.response.data.message + ":     " + s);
              } else {
                this.$bvToast.toast(err.response.data.message);
              }
            } else {
              this.$bvToast.toast("An error occured: " + JSON.stringify(err));
            }
            this.editLoading = false;
          });
      }
    }
  }
};
</script>

<style>
#editblog {
  width: 90%;
}
img {
  width: 100%;
}
</style>
